import $api from "../http";
import { AxiosResponse } from "axios";
import { AuthResponse } from "../models/response/AuthResponse";

export default class newFairService {
  static async addNewFair(
    municipality: string,
    adress: string,
    contact: string,
    dateStart: string,
    dateEnd: string,
    location: string,
    organizer: string,
    timeEnd: string,
    timeStart: string,
    tradingPlaces: string,
    type: string
  ): Promise<AxiosResponse<AuthResponse>> {
    return $api.post<AuthResponse>("/newForm", {
      municipality,
      adress,
      contact,
      dateStart,
      dateEnd,
      location,
      organizer,
      timeEnd,
      timeStart,
      tradingPlaces,
      type,
    });
  }
}
