import $api from "../http";
import { AxiosResponse } from "axios";
import { AuthResponse } from "../models/response/AuthResponse";
import { IFair } from "../models/IFair";

export default class editFairService {
  static fetchFairs(editId): Promise<AxiosResponse<IFair[]>> {
    return $api.get<IFair[]>(`/editForm/${editId}`);
  }

  static deleteEditFair(editId): Promise<AxiosResponse<IFair[]>> {
    return $api.post<IFair[]>(`/editForm/deleteEditFair/${editId}`);
  }

  static async editFairSave(
    id: string,
    adress: string,
    contact: string,
    dateStart: string,
    dateEnd: string,
    location: string,
    organizer: string,
    timeEnd: string,
    timeStart: string,
    tradingPlaces: string,
    type: string
  ): Promise<AxiosResponse<AuthResponse>> {
    return $api.post<AuthResponse>("/editFairSave", {
      id,
      adress,
      contact,
      dateStart,
      dateEnd,
      location,
      organizer,
      timeEnd,
      timeStart,
      tradingPlaces,
      type,
    });
  }
}
