import { FC, useContext, useEffect } from "react";
import { Context } from "./index";
import { observer } from "mobx-react-lite";
import { Routes, Route } from "react-router-dom";

import AuthMain from "./pages/AuthMain";
import TableMain from "./pages/TableMain";
import RegMain from "./pages/RegMain";
import Headers from "./components/Headers";
import EditForm from "./components/EditForm";
import NewForm from "./components/NewForm";

const App: FC = () => {
  const { store } = useContext(Context);

  useEffect(() => {
    if (localStorage.getItem("token")) {
      store.checkAuth();
    }
  }, []);

  if (store.isLoading) {
    return <div>...</div>;
  }

  if (!store.isAuth) {
    return (
      <Routes>
        <Route path="/" element={<AuthMain />}></Route>;
        <Route path="/registration" element={<RegMain />}></Route>;
        <Route path="*" element={<AuthMain />}></Route>;
      </Routes>
    );
  }

  if (store.isAuth) {
    return (
      <>
        <Headers />
        <Routes>
          <Route path="/" element={<TableMain />}></Route>;
          <Route path="/editForm/:id" element={<EditForm />}></Route>;
          <Route path="/newForm" element={<NewForm />}></Route>;
          <Route path="*" element={<TableMain />}></Route>;
        </Routes>
      </>
    );
  }
};

export default observer(App);
