import React, { FC, useContext, useState, useEffect } from "react";

import { Context } from "../index";
import { observer } from "mobx-react-lite";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import { useNavigate } from "react-router";
import Button from "@mui/material/Button";
import SendIcon from "@mui/icons-material/Send";

import InputMask from "react-input-mask";

import {
  useForm,
  Controller,
  SubmitHandler,
  useFormState,
} from "react-hook-form";

import "./Form.css";

interface IsignInForm {
  adress: string;
  location: string;
  contact: string;
  organizer: string;
  type: string;
  dateStart: string;
  dateEnd: string;
  timeStart: string;
  timeEnd: string;
  tradingPlaces: string;
}

const NewForm: FC = () => {
  const [show, setShow] = useState(false);
  const [nav, setNav] = useState(false);
  const { store } = useContext(Context);
  const { handleSubmit, control } = useForm<IsignInForm>();
  const { errors } = useFormState({
    control,
  });

  const navigate = useNavigate();
  if (nav) {
    setTimeout(() => {
      navigate("/");
    }, 0);
  }
  const redirect = () => {
    setNav(!nav);
  };

  const onSubmit: SubmitHandler<IsignInForm> = async (data) => {
    const success = store.newFairSave(
      store.user.municipality,
      data.adress,
      data.contact,
      data.dateStart,
      data.dateEnd,
      data.location,
      data.organizer,
      data.timeEnd,
      data.timeStart,
      data.tradingPlaces,
      data.type
    );
    setNav(!nav);
  };

  const handleClose = () => setOpenModal(false);

  useEffect(() => {
    setShow(!show);
  }, []);

  return (
    <div className="editForm">
      <div className="auth-page">
        <Typography
          variant="h6"
          component="div"
          gutterBottom={true}
          className="auth-form_subtitle"
        >
            
        </Typography>

        <div className="edit-form__form">
          <form onSubmit={handleSubmit(onSubmit)}>
            <Controller
              control={control}
              name="adress"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <TextField
                  id="outlined-multiline-flexible"
                  label=""
                  multiline
                  maxRows={5}
                  size="small"
                  margin="normal"
                  className="auth-form__input"
                  fullWidth={true}
                  onChange={(e) => field.onChange(e)}
                  value={field.value}
                  error={!!errors.adress?.message}
                  inputProps={{ maxLength: 200 }}
                />
              )}
            />

            <Controller
              control={control}
              name="location"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <InputMask
                  mask="99.999999 99.999999"
                  value={field.value}
                  onChange={(e) => field.onChange(e)}
                >
                  {() => (
                    <TextField
                      label=""
                      size="small"
                      margin="normal"
                      className="auth-form__input"
                      fullWidth={true}
                      error={!!errors.location?.message}
                    />
                  )}
                </InputMask>
              )}
            />

            <Controller
              control={control}
              name="contact"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <InputMask
                  mask="+7 (999) 999 99 99"
                  value={field.value}
                  onChange={(e) => field.onChange(e)}
                >
                  {() => (
                    <TextField
                      label=""
                      size="small"
                      margin="normal"
                      className="auth-form__input"
                      fullWidth={true}
                      error={!!errors.location?.message}
                    />
                  )}
                </InputMask>
              )}
            />

            <Controller
              control={control}
              name="organizer"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <TextField
                  label=""
                  size="small"
                  margin="normal"
                  className="auth-form__input"
                  fullWidth={true}
                  onChange={(e) => field.onChange(e)}
                  value={field.value}
                  error={!!errors.organizer?.message}
                  inputProps={{ maxLength: 60 }}
                />
              )}
            />

            <Controller
              control={control}
              name="type"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <TextField
                  label=""
                  size="small"
                  margin="normal"
                  className="auth-form__input"
                  fullWidth={true}
                  onChange={(e) => field.onChange(e)}
                  error={!!errors.type?.message}
                  value={field.value}
                  inputProps={{ maxLength: 100 }}
                />
              )}
            />
            <div>
              <Controller
                control={control}
                name="dateStart"
                rules={{
                  required: "  !",
                }}
                render={({ field }) => (
                  <InputMask
                    mask="99.99.9999"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label=" "
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        error={!!errors.dateStart?.message}
                        sx={{
                          marginRight: 2,
                        }}
                      />
                    )}
                  </InputMask>
                )}
              />

              <Controller
                control={control}
                name="dateEnd"
                rules={{
                  required: "  !",
                }}
                render={({ field }) => (
                  <InputMask
                    mask="99.99.9999"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label=" "
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        error={!!errors.dateEnd?.message}
                        sx={{
                          marginRight: 2,
                        }}
                      />
                    )}
                  </InputMask>
                )}
              />
            </div>
            <div>
              <Controller
                control={control}
                name="timeStart"
                rules={{
                  required: "  !",
                }}
                render={({ field }) => (
                  <InputMask
                    mask="99:99"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label="  "
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        error={!!errors.timeStart?.message}
                        sx={{
                          marginRight: 2,
                        }}
                      />
                    )}
                  </InputMask>
                )}
              />

              <Controller
                control={control}
                name="timeEnd"
                rules={{
                  required: "  !",
                }}
                render={({ field }) => (
                  <InputMask
                    mask="99:99"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label="  "
                        size="small"
                        error={!!errors.timeEnd?.message}
                        margin="normal"
                        className="auth-form__input"
                        sx={{
                          marginRight: 2,
                        }}
                      />
                    )}
                  </InputMask>
                )}
              />
            </div>
            <Controller
              control={control}
              name="tradingPlaces"
              rules={{
                required: "  !",
              }}
              render={({ field }) => (
                <InputMask
                  mask="999"
                  maskChar=" "
                  value={field.value}
                  onChange={(e) => field.onChange(e)}
                >
                  {() => (
                    <TextField
                      label=" "
                      size="small"
                      error={!!errors.timeEnd?.message}
                      margin="normal"
                      className="auth-form__input"
                      sx={{
                        marginRight: 2,
                      }}
                    />
                  )}
                </InputMask>
              )}
            />
            <div>
              <Button
                variant="outlined"
                onClick={() => {
                  setShow(!show);
                  redirect();
                }}
                sx={{
                  marginTop: 7,
                  marginBottom: 2,
                  marginRight: 5,
                }}
              >
                
              </Button>

              <Button
                variant="contained"
                type="submit"
                endIcon={<SendIcon />}
                sx={{
                  marginTop: 7,
                  marginBottom: 2,
                  marginLeft: 5,
                }}
              >
                 
              </Button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default observer(NewForm);
