import { FC, useContext, useState, useEffect } from "react";
import { Context } from "../index";
import { observer } from "mobx-react-lite";
import { useNavigate } from "react-router";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import {
  useForm,
  Controller,
  SubmitHandler,
  useFormState,
} from "react-hook-form";
import { CSSTransition } from "react-transition-group";
import AccountCircle from "@mui/icons-material/AccountCircle";
import KeyIcon from '@mui/icons-material/Key';
import InputAdornment from '@mui/material/InputAdornment';

import "./Form.css";

interface IsignInForm {
  login: string;
  password: string;
}

const LoginForm: FC = () => {
  const [show, setShow] = useState(false);
  const [nav, setNav] = useState(false);
  const { store } = useContext(Context);
  const { handleSubmit, control, watch } = useForm<IsignInForm>();
  const { errors } = useFormState({
    control,
  });

  const navigate = useNavigate();
  if (nav) {
    setTimeout(() => {
      navigate("/registration");
    }, 300);
  }
  const redirect = () => {
    setNav(!nav);
  };

  const onSubmit: SubmitHandler<IsignInForm> = (data) =>
    store.login(watch("login"), watch("password"));

  useEffect(() => {
    setShow(!show);
  }, []);

  return (
    <div className="authMain">
      <CSSTransition in={show} classNames="alert" timeout={1000} unmountOnExit>
        <div className="auth-page">
          <p className="auth-logo">
            <img
              src="./logo.svg"
              width="150"
              height="150"
              alt=" "
            />
          </p>

          <Typography variant="h5" display="block" gutterBottom>
               
          </Typography>
          <Typography
            variant="h6"
            component="div"
            gutterBottom={true}
            className="auth-form_subtitle"
          >
            
          </Typography>

          <div className="auth-form__form">
            <form onSubmit={handleSubmit(onSubmit)}>
              <Controller
                control={control}
                name="login"
                rules={{
                  required: "  !",
                  validate: (value: string) => {
                    if (
                      !value.match(
                        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu
                      ) ||
                      value.match(/[--]/)
                    ) {
                      return "  e-mail!";
                    }
                    return true;
                  },
                }}
                render={({ field }) => (
                  <TextField
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <AccountCircle />
                        </InputAdornment>
                      ),
                    }}
                    value={field.value}
                    error={!!errors.login?.message}
                    helperText={errors.login?.message}
                    placeholder={"  "}
                  />
                )}
              />

              <Controller
                control={control}
                name="password"
                rules={{ required: "  !" }}
                render={({ field }) => (
                  <TextField
                    type="password"
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <KeyIcon />
                        </InputAdornment>
                      ),
                    }}
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.password?.message}
                    helperText={errors.password?.message}
                    placeholder={""}
                  />
                )}
              />
              <Button
                variant="contained"
                type="submit"
                fullWidth={true}
                sx={{
                  marginTop: 3,
                  marginBottom: 3,
                }}
              >
                
              </Button>

              <Button
                variant="outlined"
                onClick={() => {
                  setShow(!show);
                  redirect();
                }}
                sx={{
                  marginTop: 0,
                }}
              >
                
              </Button>
            </form>
          </div>
        </div>
      </CSSTransition>
    </div>
  );
};

export default observer(LoginForm);
