import { observer } from "mobx-react-lite";
import { FC, useContext, useState, useEffect } from "react";
import { Link } from "react-router-dom";

import UserService from "../services/UserService";
import FairService from "../services/FairService";
import { Context } from "../index";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";

import { styled } from "@mui/material/styles";

import EditIcon from "@mui/icons-material/Edit";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import NotActivated from "./NotActivated";

export const TableForm: FC = () => {
  const { store } = useContext(Context);
  const [users, setUsers] = useState();
  const [fairs, setFairs] = useState();

  async function getFairs() {
    try {
      const response = await FairService.fetchFairs();
      setFairs(response.data);
    } catch (e) {
      console.log(e);
    }
  }
  async function getUsers() {
    try {
      const response = await UserService.fetchUsers();
      setUsers(response.data);
    } catch (e) {
      console.log(e);
    }
  }

  useEffect(() => {
    getUsers();
  }, []);

  useEffect(() => {
    if (store.user.municipality) {
      getFairs();
    }
  }, [users]);

  const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      color: theme.palette.common.black,
      fontSize: 18,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 16,
    },
  }));

  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  }));

  function createData(
    id: string,
    adress: string,
    location: string,
    dateStart: string,
    dateEnd: string
  ) {
    return { id, adress, location, dateStart, dateEnd };
  }

  const rows = [];

  if (fairs) {
    fairs.forEach((fair) => {
      rows.push(
        createData(
          fair._id,
          fair.adress,
          fair.location,
          fair.dateStart,
          fair.dateEnd
        )
      );
    });
  }

  if (!store.user.isActivated) {
    return <NotActivated />;
  }

  return (

    <div>
      <div className="tableMain">
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="customized table">
            <TableHead>
              <TableRow>
                <StyledTableCell align="center">
                   
                </StyledTableCell>
                <StyledTableCell align="center"></StyledTableCell>
                <StyledTableCell align="center"> </StyledTableCell>
                <StyledTableCell align="center">
                   
                </StyledTableCell>
                <StyledTableCell align="center"></StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <StyledTableRow key={row.id}>
                  <StyledTableCell align="center">{row.adress}</StyledTableCell>
                  <StyledTableCell align="center">
                    {row.location}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {row.dateStart}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {row.dateEnd}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    <Link to={`/editForm/` + row.id}>
                      <Button
                        variant="contained"
                        startIcon={<EditIcon />}
                        sx={{
                          textTransform: "none",
                          backgroundColor: "RGB(255, 235, 205)",
                          color: "black",
                        }}
                      >
                        
                      </Button>
                    </Link>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        {rows.length === 0 && (
          <Typography variant="h6">   </Typography>
        )}
        <div className="tableMainFooter">
          <Stack spacing={2} direction="row">
            <Link to={`/newForm/`}>
              <Button
                variant="contained"
                type="submit"
                sx={{
                  marginTop: 2,
                  marginBottom: 2,
                }}
              >
                  
              </Button>
            </Link>
          </Stack>
        </div>
      </div>
    </div>
  );
};

export default observer(TableForm);
