import React, { FC, useContext, useState, useEffect } from "react";

import { Context } from "../index";
import { observer } from "mobx-react-lite";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { useNavigate } from "react-router";
import Button from "@mui/material/Button";
import SendIcon from "@mui/icons-material/Send";
import DeleteIcon from "@mui/icons-material/Delete";

import InputMask from "react-input-mask";

import {
  useForm,
  Controller,
  SubmitHandler,
  useFormState,
} from "react-hook-form";

import editFairService from "../services/editFairService";

import "./Form.css";

interface IsignInForm {
  adress: string;
  location: string;
  contact: string;
  organizer: string;
  type: string;
  dateStart: string;
  dateEnd: string;
  timeStart: string;
  timeEnd: string;
  tradingPlaces: string;
}

const EditForm: FC = () => {
  const [editFair, setEditFair] = useState();
  const [openModal, setOpenModal] = useState(false);
  const [show, setShow] = useState(false);
  const [nav, setNav] = useState(false);
  const { store } = useContext(Context);
  const { handleSubmit, control, setValue } = useForm<IsignInForm>();
  const { errors } = useFormState({
    control,
  });

  const styleModal = {
    position: "absolute" as "absolute",
    textAlign: "center",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 500,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
    borderRadius: 10,
  };

  const navigate = useNavigate();
  if (nav) {
    setTimeout(() => {
      navigate("/");
    }, 0);
  }
  const redirect = () => {
    setNav(!nav);
  };

  const onSubmit: SubmitHandler<IsignInForm> = async (data) => {
    const id = window.location.pathname.split("/")[2];
    const success = store.editFairSave(
      id,
      data.adress,
      data.contact,
      data.dateStart,
      data.dateEnd,
      data.location,
      data.organizer,
      data.timeEnd,
      data.timeStart,
      data.tradingPlaces,
      data.type
    );
    setNav(!nav);
  };

  const handleClose = () => setOpenModal(false);

  async function getEditFair() {
    try {
      const response = await editFairService.fetchFairs(
        window.location.pathname.split("/")[2]
      );
      setEditFair(response.data);
    } catch (e) {
      console.log(e);
    }
  }

  async function deleteEditFair() {
    try {
      const response = await editFairService.deleteEditFair(
        window.location.pathname.split("/")[2]
      );
      redirect();
    } catch (e) {
      console.log(e);
    }
  }

  useEffect(() => {
    getEditFair();
  }, []);

  useEffect(() => {
    setShow(!show);
  }, []);

  useEffect(() => {
    if (editFair) {
      setValue("adress", `${editFair.adress}`);
      setValue("location", `${editFair.location}`);
      setValue("contact", `${editFair.contact}`);
      setValue("organizer", `${editFair.organizer}`);
      setValue("type", `${editFair.type}`);
      setValue("dateStart", `${editFair.dateStart}`);
      setValue("dateEnd", `${editFair.dateEnd}`);
      setValue("timeStart", `${editFair.timeStart}`);
      setValue("timeEnd", `${editFair.timeEnd}`);
      setValue("tradingPlaces", `${editFair.tradingPlaces}`);
    }
  }, [editFair]);

  if (editFair) {
    return (
      <div className="editForm">
        <div className="auth-page">
          <Typography
            variant="h6"
            component="div"
            gutterBottom={true}
            className="auth-form_subtitle"
          >
             
          </Typography>

          <div className="edit-form__form">
            <form onSubmit={handleSubmit(onSubmit)}>
              <Controller
                control={control}
                name="adress"
                render={({ field }) => (
                  <TextField
                    id="outlined-multiline-flexible"
                    label=""
                    multiline
                    maxRows={5}
                    defaultValue=" ..."
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.firstName?.message}
                    inputProps={{ maxLength: 200 }}
                  />
                )}
              />

              <Controller
                control={control}
                name="location"
                render={({ field }) => (
                  <InputMask
                    mask="99.999999 99.999999"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label=""
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        fullWidth={true}
                      />
                    )}
                  </InputMask>
                )}
              />

              <Controller
                control={control}
                name="contact"
                render={({ field }) => (
                  <InputMask
                    mask="+7 (999) 999 99 99"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label=""
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        fullWidth={true}
                      />
                    )}
                  </InputMask>
                )}
              />

              <Controller
                control={control}
                name="organizer"
                render={({ field }) => (
                  <TextField
                    label=""
                    size="small"
                    margin="normal"
                    defaultValue=" ..."
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.login?.message}
                    inputProps={{ maxLength: 60 }}
                  />
                )}
              />

              <Controller
                control={control}
                name="type"
                render={({ field }) => (
                  <TextField
                    label=""
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    defaultValue=" ..."
                    value={field.value}
                    inputProps={{ maxLength: 100 }}
                  />
                )}
              />
              <div>
                <Controller
                  control={control}
                  name="dateStart"
                  render={({ field }) => (
                    <InputMask
                      mask="99.99.9999"
                      value={field.value}
                      onChange={(e) => field.onChange(e)}
                    >
                      {() => (
                        <TextField
                          label=" "
                          size="small"
                          margin="normal"
                          className="auth-form__input"
                          sx={{
                            marginRight: 2
                          }}
                        />
                      )}
                    </InputMask>
                  )}
                />

                <Controller
                  control={control}
                  name="dateEnd"
                  render={({ field }) => (
                    <InputMask
                      mask="99.99.9999"
                      value={field.value}
                      onChange={(e) => field.onChange(e)}
                    >
                      {() => (
                        <TextField
                          label=" "
                          size="small"
                          margin="normal"
                          className="auth-form__input"
                        />
                      )}
                    </InputMask>
                  )}
                />
              </div>
              <div>
                <Controller
                  control={control}
                  name="timeStart"
                  render={({ field }) => (
                    <InputMask
                      mask="99:99"
                      value={field.value}
                      onChange={(e) => field.onChange(e)}
                    >
                      {() => (
                        <TextField
                          label="  "
                          size="small"
                          margin="normal"
                          className="auth-form__input"
                          sx={{
                            marginRight: 2
                          }}
                        />
                      )}
                    </InputMask>
                  )}
                />

                <Controller
                  control={control}
                  name="timeEnd"
                  render={({ field }) => (
                    <InputMask
                      mask="99:99"
                      value={field.value}
                      onChange={(e) => field.onChange(e)}
                    >
                      {() => (
                        <TextField
                          label="  "
                          size="small"
                          margin="normal"
                          className="auth-form__input"
                        />
                      )}
                    </InputMask>
                  )}
                />
              </div>
              <Controller
                control={control}
                name="tradingPlaces"
                render={({ field }) => (
                  <InputMask
                    mask="999"
                    maskChar=" "
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label=" "
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                      />
                    )}
                  </InputMask>
                )}
              />
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <Button
                  variant="outlined"
                  startIcon={<DeleteIcon />}
                  color="error"
                  onClick={() => {
                    setOpenModal(true);
                  }}
                  sx={{
                    marginTop: 7,
                    marginBottom: 2,
                  }}
                >
                   
                </Button>

                <Button
                  variant="outlined"
                  onClick={() => {
                    setShow(!show);
                    redirect();
                  }}
                  sx={{
                    marginTop: 7,
                    marginBottom: 2,
                  }}
                >
                  
                </Button>

                <Button
                  variant="contained"
                  type="submit"
                  endIcon={<SendIcon />}
                  sx={{
                    marginTop: 7,
                    marginBottom: 2,
                  }}
                >
                   
                </Button>
              </div>
            </form>
          </div>
        </div>
        <Modal
          open={openModal}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={styleModal}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
               
            </Typography>
            <Button
              variant="contained"
              type="submit"
              onClick={() => {
                setOpenModal(false);
              }}
              sx={{
                marginTop: 2,
                marginBottom: 2,
                marginRight: 3,
              }}
            >
              , 
            </Button>

            <Button
              variant="contained"
              type="submit"
              color="error"
              sx={{}}
              onClick={() => {
                deleteEditFair();
              }}
            >
              , 
            </Button>
          </Box>
        </Modal>
      </div>
    );
  }
};

export default observer(EditForm);
