import { FC, useContext, useState, useEffect } from "react";
import { Context } from "../index";
import { observer } from "mobx-react-lite";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { useNavigate } from "react-router";
import Button from "@mui/material/Button";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";

import InputMask from "react-input-mask";

import {
  useForm,
  Controller,
  SubmitHandler,
  useFormState,
} from "react-hook-form";
import { CSSTransition } from "react-transition-group";

import "./Form.css";

interface IsignInForm {
  login: string;
  password: string;
  firstName: string;
  lastName: string;
  phone: string;
}

const RegisterForm: FC = () => {
  const [errMessageReg, setErrMessageReg] = useState("");
  const [openModal, setOpenModal] = useState(false);
  const [show, setShow] = useState(false);
  const [nav, setNav] = useState(false);
  const [municipality, setMunicipality] = useState("");
  const { store } = useContext(Context);
  const { handleSubmit, control} = useForm<IsignInForm>();
  const { errors } = useFormState({
    control,
  });

  const styleModal = {
    position: "absolute" as "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
    borderRadius: 10,
  };

  const navigate = useNavigate();
  if (nav) {
    setTimeout(() => {
      navigate("/");
    }, 300);
  }
  const redirect = () => {
    setNav(!nav);
  };

  const onSubmit: SubmitHandler<IsignInForm> = async (data) => {
    const success = store.registration(
      data.firstName,
      data.lastName,
      data.phone,
      municipality,
      data.login,
      data.password
    );
    if (success.then((data) => data?.flag)) {
      await success.then((data) => setErrMessageReg(data?.message));
      setOpenModal(true);
    }
  };
  const handleChange = (event: SelectChangeEvent) => {
    setMunicipality(event.target.value);
  };

  const handleClose = () => setOpenModal(false);

  useEffect(() => {
    setShow(!show);
  }, []);

  return (
    <div className="authMain">
      <CSSTransition in={show} classNames="alert" timeout={1000} unmountOnExit>
        <div className="auth-page">
          <Typography variant="h5" display="block" gutterBottom>
               
          </Typography>
          <Typography
            variant="h6"
            component="div"
            gutterBottom={true}
            className="auth-form_subtitle"
          >
              
          </Typography>

          <div className="reg-form__form">
            <form onSubmit={handleSubmit(onSubmit)}>
              <Controller
                control={control}
                name="firstName"
                rules={{
                  required: "  !",
                  validate: (value: string) => {
                    if (!value.match(/[--]/)) {
                      return " ";
                    }
                    return true;
                  },
                }}
                render={({ field }) => (
                  <TextField
                    label=""
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.firstName?.message}
                    placeholder={" "}
                    inputProps={{ maxLength: 20 }}
                  />
                )}
              />

              <Controller
                control={control}
                name="lastName"
                rules={{
                  required: "  !",
                  validate: (value: string) => {
                    if (!value.match(/[--]/)) {
                      return " ";
                    }
                    return true;
                  },
                }}
                render={({ field }) => (
                  <TextField
                    label=""
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.lastName?.message}
                    placeholder={" "}
                    inputProps={{ maxLength: 20 }}
                  />
                )}
              />

              <Controller
                control={control}
                name="phone"
                render={({ field }) => (
                  <InputMask
                    mask="+7 (999) 999 99 99"
                    value={field.value}
                    onChange={(e) => field.onChange(e)}
                  >
                    {() => (
                      <TextField
                        label="  "
                        size="small"
                        margin="normal"
                        className="auth-form__input"
                        fullWidth={true}
                      />
                    )}
                  </InputMask>
                )}
              />

              <FormControl variant="standard" required={true} sx={{ m: 1, minWidth: 300 }}>
                <InputLabel>
                   
                </InputLabel>
                <Select
                  value={municipality}
                  onChange={handleChange}
                  label="municipality"
                >
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={"-"}>-</MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={"-"}>
                    -
                  </MenuItem>
                  <MenuItem value={" "}>
                     
                  </MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={"-"}>
                    -
                  </MenuItem>
                  <MenuItem value={" "}>
                     
                  </MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                  <MenuItem value={""}></MenuItem>
                </Select>
              </FormControl>

              <Controller
                control={control}
                name="login"
                rules={{
                  required: "  !",
                  validate: (value: string) => {
                    if (
                      !value.match(
                        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu
                      ) ||
                      value.match(/[--]/)
                    ) {
                      return "  e-mail!";
                    }
                    return true;
                  },
                }}
                render={({ field }) => (
                  <TextField
                    label=" "
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.login?.message}
                    placeholder={"  "}
                    inputProps={{ maxLength: 40 }}
                  />
                )}
              />

              <Controller
                control={control}
                name="password"
                rules={{ required: "  !" }}
                render={({ field }) => (
                  <TextField
                    label=""
                    type="password"
                    size="small"
                    margin="normal"
                    className="auth-form__input"
                    fullWidth={true}
                    onChange={(e) => field.onChange(e)}
                    value={field.value}
                    error={!!errors.password?.message}
                    inputProps={{ maxLength: 20 }}
                  />
                )}
              />
                <Button
                  variant="contained"
                  type="submit"
                  fullWidth={true}
                  sx={{
                    marginTop: 2,
                    marginBottom: 2,
                  }}
                >
                   
                </Button>



                <Button
                  variant="outlined"
                  onClick={() => {
                    setShow(!show);
                    redirect();
                  }}
                  sx={{
                    marginTop: 0,
                  }}
                >
                  
                </Button>

            </form>
          </div>
        </div>
      </CSSTransition>
      <Modal
        open={openModal}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={styleModal}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
             !
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            {errMessageReg && errMessageReg}
          </Typography>
        </Box>
      </Modal>
    </div>
  );
};

export default observer(RegisterForm);
