import { observer } from "mobx-react-lite";
import { FC, useContext} from "react";
import LogoutIcon from "@mui/icons-material/Logout";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { Context } from "../index";

import "./Form.css";

const NotActivated: FC = () => {
  const { store } = useContext(Context);

  return (
    <div className="authMain">
      <div className="auth-page">
        <p className="auth-logo">
          <img
            src="./logo.svg"
            width="150"
            height="150"
            alt=" "
          />
        </p>

        <Typography
          variant="h6"
          component="div"
          gutterBottom={true}
          className="auth-form_subtitle"
          sx={{ marginBottom: 2 }}
        >
             
        </Typography>
        <Typography variant="subtitle2" component="div" gutterBottom={true}>
                 :
        </Typography>
        <Typography
          variant="subtitle2"
          component="div"
          gutterBottom={true}
          sx={{ marginBottom: 5 }}
        >
          +7 (498) 602-30-90, . , msh@mosreg.ru
        </Typography>
        <Button
          startIcon={<LogoutIcon />}
          variant="outlined"
          onClick={() => {
            store.logout();
          }}
          sx={{
            marginTop: 0,
          }}
        >
             
        </Button>
      </div>
    </div>
  );
};

export default observer(NotActivated);
