import { IUser } from "../models/IUser";
import { makeAutoObservable } from "mobx";
import AuthService from "../services/AuthService";
import editFairService from "../services/editFairService";
import newFairService from "../services/newFairService";
import axios from "axios";
import { AuthResponse } from "../models/response/AuthResponse";
import { API_URL } from "../http";

export default class Store {
  user = {} as IUser;
  isAuth = false;
  isLoading = false;

  constructor() {
    makeAutoObservable(this);
  }

  setAuth(bool: boolean) {
    this.isAuth = bool;
  }

  setUser(user: IUser) {
    this.user = user;
  }

  setLoading(bool: boolean) {
    this.isLoading = bool;
  }

  async login(email: string, password: string, municipality: string) {
    try {
      const response = await AuthService.login(email, password, municipality);
      console.log(response);
      localStorage.setItem("token", response.data.accessToken);
      this.setAuth(true);
      this.setUser(response.data.user);
    } catch (e) {
      console.log(e.response?.data?.message);
    }
  }

  async registration(
    firstName: string,
    lastName: string,
    phone: string,
    municipality: string,
    email: string,
    password: string
  ) {
    try {
      const response = await AuthService.registration(
        firstName,
        lastName,
        phone,
        municipality,
        email,
        password
      );
      console.log(response);
      localStorage.setItem("token", response.data.accessToken);
      this.setAuth(true);
      this.setUser(response.data.user);
    } catch (e) {
      console.log(e.response?.data?.message);
      return {
        flag: false,
        message: e.response?.data?.message,
      };
    }
  }

  async editFairSave(
    id: string,
    adress: string,
    contact: string,
    dateStart: string,
    dateEnd: string,
    location: string,
    organizer: string,
    timeEnd: string,
    timeStart: string,
    tradingPlaces: string,
    type: string
  ) {
    try {
      const response = await editFairService.editFairSave(
        id,
        adress,
        contact,
        dateStart,
        dateEnd,
        location,
        organizer,
        timeEnd,
        timeStart,
        tradingPlaces,
        type
      );
    } catch (e) {
      console.log(e.response?.data?.message);
      return {
        flag: false,
        message: e.response?.data?.message,
      };
    }
  }

  async newFairSave(
    municipality: string,
    adress: string,
    contact: string,
    dateStart: string,
    dateEnd: string,
    location: string,
    organizer: string,
    timeEnd: string,
    timeStart: string,
    tradingPlaces: string,
    type: string
  ) {
    try {
      const response = await newFairService.addNewFair(
        municipality,
        adress,
        contact,
        dateStart,
        dateEnd,
        location,
        organizer,
        timeEnd,
        timeStart,
        tradingPlaces,
        type
      );
    } catch (e) {
      console.log(e.response?.data?.message);
      return {
        flag: false,
        message: e.response?.data?.message,
      };
    }
  }

  async logout() {
    try {
      const response = await AuthService.logout();
      localStorage.removeItem("token");
      this.setAuth(false);
      this.setUser({} as IUser);
    } catch (e) {
      console.log(e.response?.data?.message);
    }
  }

  async checkAuth() {
    this.setLoading(true);
    try {
      const response = await axios.get<AuthResponse>(`${API_URL}/refresh`, {
        withCredentials: true,
      });
      console.log(response);
      localStorage.setItem("token", response.data.accessToken);
      this.setAuth(true);
      this.setUser(response.data.user);
    } catch (e) {
      console.log(e.response?.data?.message);
    } finally {
      this.setLoading(false);
    }
  }
}
